@import url('https://fonts.googleapis.com/css2?family=Geologica:wght@100;200;300;400;500;600;700;800;900&family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap');

body {
    font-family: 'inter';
    /* background-image: linear-gradient(45deg, #E7FEDA, #00d4ff); */
    /* background-color: #00d4ff; */
}

.navigation {
    display: flex;
    justify-content: space-between;
    align-content: center;
    height: auto;
    padding: 30px 35px 20px 35px;
    /* border: 1px solid black; */
}

.logo {
    /* margin: 20px 20px 10px 30px; */
    font-size: 1.2rem;
    font-weight: 700;
    color: black;
    text-decoration: none;
}

.navigationList {
    /* margin: 20px 30px 10px 20px; */
    display: flex;
    align-content: center;
}

.navigationItem {
    padding-left: 70px;
    color: black;
    text-decoration: none;
}

.mainSection {
    background-color: aquamarine;
    padding: 40px 20px;
    display: flex;
    justify-content: space-between;
    height: auto;
    margin: 10px;
    border-radius: 15px;
}

.mainTitle {
    font-size: 4rem;
    font-weight: 800;
    padding-right: 100px;
}

.introduction {
    font-size: 1rem;
    width: 500px;
    line-height: 35px;
}

footer {
    background-color: aquamarine;
    color: black;
    padding: 40px 20px;
    display: flex;
    justify-content: space-between;
    height: auto;
    margin: 10px;
    border-radius: 15px;
}

.footerList {
    /* margin: 20px 30px 10px 20px; */
    display: flex;
    align-content: center;
}

.footerItem {
    padding-left: 70px;
}